@import url(https://fonts.googleapis.com/css2?family=Flamenco&display=swap);
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}


body {
  min-height: 100vh;
  position: relative;
}

.stars, .twinkling, .clouds {
  position: absolute;
  display: block;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  width: 100%;
  height: 100%;
}

.stars {
  z-index: -4;
  background: #000 url(/projects/colorstella/static/media/stars.90c266f7.png) repeat fixed center;
}

.twinkling {
  z-index: -3;
  background: transparent url(data:image/png;base64,) repeat fixed center;
  animation: move-twink-back 200s linear infinite;
}

@keyframes move-twink-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: -10000px 5000px;
  }
}

@keyframes move-clouds-back {
  from {
    background-position: 0 0;
  }
  to {
    background-position: 10000px 0;
  }
}
